a(0,255,255,.2)}}@keyframes flash-3{0%,100%{background:#f70094;box-shadow:0px 4.6666666667px 24px 3px #f70094}50%{background:rgba(247,0,148,.4);box-shadow:0px 4.6666666667px 24px 3px rgba(247,0,148,.2)}}
.IeWarning-Container{text-align:center;border:5px red solid;background-color:pink;padding:50px;font-weight:bold;font-size:larger}
